@-webkit-keyframes rollEgg{
    0%{
    -webkit-transform:rotate(0deg);transform:rotate(0deg)
}
2%{
    -webkit-transform:rotate(7deg);transform:rotate(7deg)
}
6%{
    -webkit-transform:rotate(-7deg);transform:rotate(-7deg)
}
10%{
    -webkit-transform:rotate(7deg);transform:rotate(7deg)
}
14%{
    -webkit-transform:rotate(-7deg);transform:rotate(-7deg)
}
16%{
    -webkit-transform:rotate(0deg);transform:rotate(0deg)
}
to{
    -webkit-transform:rotate(0deg);transform:rotate(0deg)
}

}
@keyframes rollEgg{
    0%{
    -webkit-transform:rotate(0deg);transform:rotate(0deg)
}
